.App{
    
    height: 100vh;


    .section{

        width: 100%;
        height: calc(100vh - 70px);
        background-color: rgb(250, 235, 25);
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
      
        
       

        > *{ 
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start ;
            
        }
    }
}